body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}



.App {
  text-align: center;
}

.appTitle {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.current {
  border-bottom: 4px solid white;
}

button {
  padding-left: 0px;
  padding-right: 0px;
  background-color: white;
  color: #4b2e83;
  border-radius: 5px;
  border-color: #4b2e83
}

hr {
  border-top: solid 1px #4b2e83;
}

.appHeader {
  background-color: #4b2e83;
  color: white;
  height: 65px;
}

#HomeLink img {
  margin-top: 6px;
  margin-left: 10px;
  width: 220px;
  float: left;
}

#links {
  float: right;
  padding-top: 20px;
  margin-right: 20px;
  color: white;
}

.navLink:hover {
  color: #b7a57a !important;
}

a {
  text-decoration: none;
  padding: 5px;
}

.navLink {
  color: white !important;
  margin: 10px;
}

.curRoute {
  border-bottom: 1px solid white;
}



/* .home-wrapper {
    margin: 'auto';
    width: '57%';

} */

@media only screen and (max-width: 400px) {
    .home-wrapper {
        /* margin: 'auto'; */
        width: '80%';
    }

    .flow-text {
        font-size: 1.2rem;
    }
}

.title {
    font-weight: bolder;
}

.italic-light {
    font-style: italic;
    font-weight: 300;
}

.quote {
    text-align: justify;
    font-style: italic;
    font-weight: 300;
}

svg text {
    font-family: "FontAwesome";
 }

 .center-align {
     text-align: center;
 }

 .responsive-img {
     width: 70%;
     height: auto;
     margin-left: auto;
     margin-right: auto;
     text-align: center;
 }

 h1, h2, h3, h4, h5, h6 {
    font-weight: 400;
    line-height: 1.3;
}

 h1 {
     font-size: 1.3em;
 }

 h2 {
    font-size: 3.56rem;
    line-height: 110%;
 }

 h3 {
    font-size: 2.92rem;
    line-height: 110%;
 }

 .flow-text {
    font-size: 1.68rem;
 }
